.Card {
    border-radius: 4px;
    height: 180px;
    margin: 3px;
    position: relative;
    width: 140px;
}

.Card-clickable {
    cursor: pointer;
}

.Card-container {
    height: 100%;
    position: absolute;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: all 1s ease-in-out;
    -o-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;
    width: 100%;
}

.Card-flipped .Card-container {
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
}

.Card-side {
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    border-radius: 4px;
    height: 100%;
    overflow: hidden;
    position: absolute;
    width: 100%;
}

.Card-back {
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(147,206,222,1)), color-stop(41%, rgba(117,189,209,1)), to(rgba(18,33,199,1)));
    background: -webkit-linear-gradient(top, rgba(147,206,222,1) 0%, rgba(117,189,209,1) 41%, rgba(18,33,199,1) 100%);
    background: -o-linear-gradient(top, rgba(147,206,222,1) 0%, rgba(117,189,209,1) 41%, rgba(18,33,199,1) 100%);
    background: linear-gradient(to bottom, rgba(147,206,222,1) 0%, rgba(117,189,209,1) 41%, rgba(18,33,199,1) 100%);
}

.Card-face {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

.Card-title {
    position: absolute;
    bottom: 0;
    text-align: center;
    width: 100%;
    color: #ffffff;
    padding-bottom: 3px;
    background-color: #262c37;
}


